import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'

const Header = ({ siteTitle }) => (
      <h1 style={{
        padding: `1rem 4.5rem`,
        backgroundColor: '#4a70dc',
      }}>  
        <Link  to="/" style={{
          color: `white`,
          textDecoration: `none`,
        }}>
        
          {siteTitle}
        </Link>
        
      </h1>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
